export default {
  computed: {
    modal () {
      return this.$store.state.modal.modal
    }
  },
  methods: {
    changeModal (val) {
      this.$store.commit('modal/changeModal', val)
      setTimeout(() => {
        this.$store.commit('modal/clearModal')
      }, 500)
    }
  }
}
