export default {
  name: 'Header',
  data () {
    return {
      nav: [
        {
          label: 'Inicio',
          link: '/',
          pdf: false
        },
        {
          label: 'Agenda',
          link: 'agenda.pdf',
          pdf: true
        },
        {
          label: 'Comité Organizador',
          link: '/comite-organizador',
          pdf: false
        },
        {
          label: 'Comité Arbitral',
          link: '/comite-arbitral',
          pdf: false
        },
        {
          label: 'Convocatoria',
          link: 'convocatoria.pdf',
          pdf: true
        },
        {
          label: 'Para el Autor',
          link: '/para-el-autor',
          pdf: false
        },
        {
          label: 'Contacto',
          link: '/contacto',
          pdf: false
        }
      ],
      scrollPosition: null,
      pivot: 0
    }
  },
  methods: {
    updateScroll() {
       this.scrollPosition = window.scrollY
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
    const pivotEl = document.querySelector('.hero.relative')
    if(pivotEl) {
      this.pivot = pivotEl.clientHeight
    }
  }
}
